import axios from 'axios';

axios.defaults.baseURL = 'http://82.146.40.11:5000'


export const api = {
    async setFilter(filename, filter) {
        try {
            const { data } = await axios.post('/api/files/filter', { filename, filter })
            return data
        } catch (error) {
            console.log('error', error)
        }
    },
    async saveFile(filename, filter) {
        try {
            const { data } = await axios.post('/api/files/export', { filename, filter })
            return data
        } catch (error) {
            console.log('error', error)
        }
    },
    async getFileByFilename(filename) {
        try {
            const { data } = await axios.post('/api/files/filename', { filename })
            return data
        } catch (error) {
            console.log('error', error)
            return error
        }
    },
}