import { useState } from "react";

const LOCALSTORAGE_NAME = 'datalize'

export const useLocalStorage = () => {

    const [files, setFiles] = useState([])

    const setToLocalStorage = filename => {
        const storage = JSON.parse(localStorage.getItem(LOCALSTORAGE_NAME));
        if (!storage) {
            localStorage.setItem(LOCALSTORAGE_NAME, JSON.stringify({ files: [filename] }));
        } else {
            const isFile = storage.files.find(file => filename === file)
            if (!isFile) {
                localStorage.setItem(LOCALSTORAGE_NAME, JSON.stringify({ files: [...storage.files, filename] }));
            }
        }
    }

    const getFilesFromLocalStorage = () => {
        const storage = JSON.parse(localStorage.getItem(LOCALSTORAGE_NAME));
        if (storage && storage.files) {
            setFiles(storage.files)
        }
    }

    return {
        files,
        setToLocalStorage,
        getFilesFromLocalStorage
    }
}