import { useState } from "react"
import exportFromJSON from 'export-from-json'
import { api } from '../config/api';
import { makeFilename } from '../utils/utils';
import { LOCALSTORAGE_NAME } from "../config/constants";
import { useLocalStorage } from './useLocalStorage'



export const useData = () => {

    const [data, setData] = useState(null)
    const [filter, setFilter] = useState([])
    const [filename, setFilename] = useState(null)
    const [loading, setLoading] = useState(false)
    const [error, setError] = useState('')

    const { files, setToLocalStorage, getFilesFromLocalStorage } = useLocalStorage()

    const filterHandler = async (newFilter, action = 'plus') => {

        if (loading) return

        let newFilters = []

        if (action === 'plus') {
            const newFiltersObject = {
                column: newFilter[0].split(':')[0],
                value: newFilter.map(item => item.split(':')[1])
            }
            newFilters = [...filter, newFiltersObject]
        } else if (action === 'minus') {
            const isExistedFilterHasOneValue = filter[newFilter.index].value.length === 1 ? true : false
            if (isExistedFilterHasOneValue) {
                newFilters = filter.filter(item => item.column !== newFilter.column)
            } else {
                const tempFilters = filter.map((item) => item.column === newFilter.column ? ({ ...item, value: item.value.filter(valueItem => valueItem !== newFilter.value) }) : item)
                newFilters = tempFilters
            }
        }

        setFilter(newFilters)
        setLoading(true)
        const result = await api.setFilter(filename, newFilters)
        setData(result.data)
        setLoading(false)
    }

    const getDataByFilename = async (filename) => {
        setError('')
        setLoading(true)
        const result = await api.getFileByFilename(filename)
        console.log("üöÄ ~ file: useData.js ~ line 53 ~ getDataByFilename ~ result", result)
        if (result.data) {
            setToLocalStorage(filename)
            setData(result.data)
            setFilename(filename)
            setLoading(false)
        } else {
            if (result.response.status === 400) {
                console.log(result.response.data.error)
                setError(result.response.data.error)
                setLoading(false)
            }
        }             
    }

    const saveFileHandler = async () => {
        setLoading(true)
        const idsJson = await api.saveFile(filename, filter)
        const fileName = makeFilename(filter)
        const exportType = 'csv'
        exportFromJSON({ data: idsJson.data, fileName, exportType })
        setLoading(false)
    }

    return { data, setData, loading, filter, filterHandler, filename, setFilename, getDataByFilename, saveFileHandler, error }

}