function formatData(date) {
    const year = date.getFullYear()
    let month = (date.getMonth() + 1).toString()
    if (month.length === 1) {
        month = '0' + month
    }
    let day = date.getDate().toString()
    if (day.length === 1) {
        day = '0' + day
    }
    return day + '-' + month + '-' + year
}

export const makeFilename = filters => {
    const today = new Date()
    const date = formatData(today)
    const filterString = filters.map(item => `${item.column}-${item.value.join(',')}`).join('__')
    const result = date + '__' + filterString
    return result
}