import { Table } from 'antd';
import { useState } from 'react';
import { Element, animateScroll as scroll } from 'react-scroll'


const columns = [
    {
        title: '–ó–Ω–∞—á–µ–Ω–∏–µ',
        dataIndex: 'name',
        key: 'name',
    },
    {
        title: '–ö–æ–ª–∏—á–µ—Å—Ç–≤–æ',
        dataIndex: 'amount',
        key: 'age',
        width: 100,
        align: 'center',
        render: text => <span>{text.toLocaleString('ru')}</span>,
    },
    {
        title: '–î–æ–ª—è',
        dataIndex: 'percent',
        key: 'percent',
        width: 100,
        align: 'center',
        render: text => text > 5 ? <span style={{ color: 'green', fontWeight: 500 }}>{text}%</span> : <span>{text}%</span>,
    },
    // {
    //     title: '–û–±—â–∞—è –¥–æ–ª—è',
    //     dataIndex: 'initial',
    //     key: 'initial',
    //     width: 150,
    //     align: 'center',
    //     render: text => text > 5 ? <span style={{ color: 'green', fontWeight: 500 }}>{text}</span> : <span>{text}</span>,
    // },

];

export const Tables = ({ data, filterHandler }) => {

    const [selectedRowKeys, setSelectedRowKeys] = useState([])

    const clickHandler = () => {
        filterHandler(selectedRowKeys)
        setSelectedRowKeys([])
    }
    // const clickHandler = (value, column) => {
    //     filterHandler({ column, value })
    // }

    const onSelectChange = (selectedRowKeys) => {
        setSelectedRowKeys(selectedRowKeys);
    };

    const rowSelection = {
        selectedRowKeys,
        onChange: onSelectChange
    };

    

    return (
        <div className='div work'>
            {
                data.map(item => {

                    const hasSelected = selectedRowKeys.length > 0 && item.column === selectedRowKeys[0].split(':')[0]

                    return (
                        <Element key={item.column} name={item.column} className="element" >

                            <div className='table'>
                                <div className='table_header'>
                                    <h3>{item.column}</h3>
                                    <span className={!hasSelected ? 'hidden' : 'button button-red'} onClick={clickHandler}>–û—Ç—Ñ–∏–ª—å—Ç—Ä–æ–≤–∞—Ç—å</span>
                                </div>
                                <Table
                                    dataSource={item.data}
                                    columns={columns}
                                    rowSelection={rowSelection}
                                    pagination={{ position: ['none'] }}
                                    // rowSelection={{        
                                    //     selectedRowKeys,
                                    //     onChange: selectedRowKeys => onSelectChange(selectedRowKeys, item.column)
                                    // }}
                                    // onRow={(record, rowIndex) => {
                                    //     return {
                                    //         onClick: event => clickHandler(event.target.innerText, item.column), // click row
                                    //     };
                                    // }}
                                />
                            </div>
                        </Element>
                    )
                })
            }

        </div>
    )
}